// 引入vue
import Vue from 'vue';
import VueRouter from 'vue-router';
import app from './App';
import vheader from 'component/mui/Header.vue';

import muiUtils from 'common/muiUtils';
import CONSTS from 'common/consts';
import pageUrl from 'api';
import vnav from 'component/mui/NavTabbar.vue';
Vue.use(VueRouter);
var router = new VueRouter();

// mui生命周期管理
mui.init({
    swipeBack: false
});

// 头部head start
var HeaderVue = Vue.extend(vheader);
/* eslint-disable no-unused-vars */
var header = new HeaderVue({
    propsData: {
        title: '采购单',
        hdstyle: {
            back: {
                display: true
            },
            title: {
                display: true
            },
            rightButton: {
                display: true,
                text: '编辑',
                onClick: function() {
                    let webview = plus.webview.currentWebview();
                    if (header.hdstyle.rightButton.text === '编辑') {
                        header.hdstyle.rightButton.text = '完成';
                        header.title = '已选择(0)';
                        mui.fire(webview, 'isedit', {
                            edit: false
                        });
                    } else {
                        header.hdstyle.rightButton.text = '编辑';
                        header.title = '采购单';
                        mui.fire(webview, 'isedit', {
                            edit: true
                        });
                    }
                } // 右侧按钮点击事件处理方法
            },
        }
    },
    el: 'vheader'
});
// 头部head end
// 监听跳转采购单
window.addEventListener('toptext', function(event) {
    header.hdstyle.rightButton.text = event.detail.toptext;
    header.header.title = event.detail.title;
});
// 监听购买数量
window.addEventListener('buytype', function(event) {
    if (header.hdstyle.rightButton.text === '编辑') {
        header.title = '采购单';
    } else {
        header.title = '已选择(' + event.detail.buytype + ')';
    }
});
mui.plusReady(function() {
    router.start(app, '#app');
});
